import React from "react";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Grow from "@material-ui/core/Grow";
import Paper from "@material-ui/core/Paper";
import Popper from "@material-ui/core/Popper";
import MenuItem from "@material-ui/core/MenuItem";
import MenuList from "@material-ui/core/MenuList";
import { makeStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";
import "./Shifts.css";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  paper: {
    marginRight: theme.spacing(2),
  },
}));

export default function MenuListComposition({ facility }) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const anchorRef = React.useRef(null);

  const handleToggle = () => {
    setOpen((prevOpen) => !prevOpen);
  };

  const handleClose = (event) => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return;
    }

    setOpen(false);
  };

  function handleListKeyDown(event) {
    if (event.key === "Tab") {
      event.preventDefault();
      setOpen(false);
    }
  }

  // return focus to the button when we transitioned from !open -> open
  const prevOpen = React.useRef(open);
  React.useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.current.focus();
    }

    prevOpen.current = open;
  }, [open]);

  return (
    <>
      {!facility ? (
        <div className={classes.root}>
          <div className="shifts__button">
            <button ref={anchorRef} aria-haspopup="true" onClick={handleToggle}>
              Shifts
            </button>
            <Popper
              open={open}
              anchorEl={anchorRef.current}
              role={undefined}
              transition
              disablePortal
            >
              {({ TransitionProps, placement }) => (
                <Grow
                  {...TransitionProps}
                  style={{
                    transformOrigin:
                      placement === "bottom" ? "center top" : "center bottom",
                  }}
                >
                  <Paper>
                    <ClickAwayListener onClickAway={handleClose}>
                      <MenuList
                        autoFocusItem={open}
                        id="menu-list-grow"
                        onKeyDown={handleListKeyDown}
                      >
                        <div className="shifts__links">
                          <Link to="/upComingShifts">
                            <MenuItem onClick={handleClose}>
                              Upcoming Shifts
                            </MenuItem>
                          </Link>
                          <Link to="/completedShifts">
                            <MenuItem onClick={handleClose}>
                              Completed Shifts
                            </MenuItem>
                          </Link>
                          <Link to="/savedShifts">
                            <MenuItem onClick={handleClose}>
                              Saved Shifts
                            </MenuItem>
                          </Link>
                        </div>

                        <MenuItem onClick={handleClose}>Pay</MenuItem>
                      </MenuList>
                    </ClickAwayListener>
                  </Paper>
                </Grow>
              )}
            </Popper>
          </div>
        </div>
      ) : (
        <div className={classes.root}>
          <div className="shifts__button">
            <button ref={anchorRef} aria-haspopup="true" onClick={handleToggle}>
              Shifts
            </button>
            <Popper
              open={open}
              anchorEl={anchorRef.current}
              role={undefined}
              transition
              disablePortal
            >
              {({ TransitionProps, placement }) => (
                <Grow
                  {...TransitionProps}
                  style={{
                    transformOrigin:
                      placement === "bottom" ? "center top" : "center bottom",
                  }}
                >
                  <Paper>
                    <ClickAwayListener onClickAway={handleClose}>
                      <MenuList
                        autoFocusItem={open}
                        id="menu-list-grow"
                        onKeyDown={handleListKeyDown}
                      >
                        <div className="shifts__links">
                          <Link to="/upComingShifts">
                            <MenuItem onClick={handleClose}>
                              Schedule History
                            </MenuItem>
                          </Link>
                          <Link to="/completedShifts">
                            <MenuItem onClick={handleClose}>
                              Schedule Nurse
                            </MenuItem>
                          </Link>
                          <Link to="/liveFeed">
                            <MenuItem onClick={handleClose}>Live Feed</MenuItem>
                          </Link>
                        </div>

                        <MenuItem onClick={handleClose}>Billing</MenuItem>
                        <MenuItem onClick={handleClose}>Shift Details</MenuItem>
                      </MenuList>
                    </ClickAwayListener>
                  </Paper>
                </Grow>
              )}
            </Popper>
          </div>
        </div>
      )}
    </>
  );
}
